/* 1. Reset & Base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Orbitron', sans-serif;
  background: #0a0a0a;
  color: #e0e0e0;
  line-height: 1.6;
}

/* 2. Layout */
.container {
  display: grid;
  grid-template-columns: 320px 1fr;
  grid-template-areas:
    "header header"
    "sidebar main";
  min-height: 100vh;
}
.header   { grid-area: header; }
.sidebar  { grid-area: sidebar; }
.main-content { grid-area: main; }

/* 3. Header & Nav */
.header {
  background: linear-gradient(135deg,#20042f,#292b00);
  border-bottom: 3px solid #ff00ff;
  box-shadow: 0 0 12px #ff00ff80;
  padding: 1rem 2rem;
}
.top-nav ul {
  list-style: none;
  display: flex;
  gap: 2rem;
}
.top-nav a {
  color: #00ffff;
  text-decoration: none;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 0.2rem;
  transition: color 0.2s;
}
.top-nav a:hover,
.top-nav a.active {
  color: #ff00ff;
}
.top-nav a:hover::after,
.top-nav a.active::after {
  content: '';
  position: absolute;
  bottom: 0; left: 0;
  width: 100%; height: 3px;
  background: #ff00ff;
  border-radius: 2px;
}

/* 4. Sidebar Profile */
.sidebar {
  background: #111122;
  padding: 2rem;
  border-right: 2px solid #00ffff;
  box-shadow: 0 0 15px #00ffff30;
}
.profile { text-align: center }
.profile-pic {
  width: 240px; height: 240px;
  border-radius: 50%;
  border: 3px solid #00ffff;
  box-shadow:
    0 0 15px #ffd70055,    /* subtle base glow */
    0 0 30px #ffd70033;    /* outer haze */
  animation: gold-pulse 3s infinite alternate ease-in-out;
}
@keyframes gold-pulse {
  from {
    box-shadow:
      0 0 10px #ffd70044,
      0 0 20px #ffd70022;
  }
  to {
    box-shadow:
      0 0 18px #ffd700aa,
      0 0 30px #ffd70077;
  }
}
.name {
  font-size: 1.8rem;
  color: #ffff00;
  text-shadow: 0 0 5px #ffff0080;
}
.username a{
  color: #ffff00;
  text-shadow: 0 0 4px #ffff0080;
  margin: 0.2rem 0 1rem;
}
.tagline {
  color: #ff00ff;
  text-shadow: 0 0 3px #ff00ff80;
}
.bio-info {
  list-style: none;
  margin-top: 1rem;
}
.bio-info li + li {
  margin-top: 0.8rem;
}
.bio-info a {
  color: #00ccff;
  text-decoration: none;
}

/* 5. Panel Wrapper for About & Projects */
.panel {
  background: radial-gradient(circle at center,#20042f,#0a0a0a 80%);
  border: 2px solid #ff00ff;
  border-radius: 12px;
  box-shadow:
    inset 0 0 40px rgba(255,0,255,0.6),
    0 0 20px rgba(0,255,255,0.4);
  padding: 2rem;
  margin: 2rem 0;
  animation: pulse 3s infinite alternate ease-in-out;
}

#about {
  color: #ffff00;
}


/* 6. Pulse Animation */
@keyframes pulse {
  from {
    box-shadow:
      inset 0 0 20px rgba(255,0,255,0.4),
      0 0 10px rgba(0,255,255,0.3);
  }
  to {
    box-shadow:
      inset 0 0 60px rgba(255,0,255,0.8),
      0 0 30px rgba(0,255,255,0.6);
  }
}

/* 7. Glitchy Section Title */
.section-title {
  position: relative;
  font-size: 2rem;
  text-transform: uppercase;
  color: #ff00ff;
  z-index: 1;
  margin-bottom: 1rem;
}
.section-title::after {
  content: '';
  position: absolute;
  bottom: -4px; left: 0;
  width: 100%; height: 2px;
  background: #ff00ff;
  box-shadow: 0 0 6px #ff00ff50;
}
.section-title[data-text]::before,
.section-title[data-text]::after {
  content: attr(data-text);
  position: absolute; top: 0; left: 0;
  width: 100%; overflow: hidden;
  clip: rect(0,900px,0,0);
}
.section-title[data-text]::before {
  text-shadow: -2px 0 #00ffff;
  animation: glitch-1 2.5s infinite linear alternate-reverse;
}
.section-title[data-text]::after {
  text-shadow: 2px 0 #ff0000;
  animation: glitch-2 3s infinite linear alternate-reverse;
}

/* Glitch keyframes */
@keyframes glitch-1 {
  0%   { clip: rect(2px,9999px,5px,0);   transform: translate(-1px,-1px); }
 25%   { clip: rect(10px,9999px,15px,0); transform: translate(1px,1px); }
 50%   { clip: rect(5px,9999px,8px,0);   transform: translate(-1px,2px); }
 75%   { clip: rect(12px,9999px,18px,0); transform: translate(2px,-1px); }
 100%  { clip: rect(3px,9999px,6px,0);   transform: translate(0); }
}
@keyframes glitch-2 {
  0%   { clip: rect(20px,9999px,25px,0); transform: translate(1px,0); }
 25%   { clip: rect(0px,9999px,4px,0);   transform: translate(-1px,1px); }
 50%   { clip: rect(15px,9999px,20px,0); transform: translate(1px,-1px); }
 75%   { clip: rect(7px,9999px,10px,0);  transform: translate(-1px,2px); }
 100%  { clip: rect(18px,9999px,22px,0); transform: translate(0); }
}

/* 8. Subtitles & Lists */
.section-subtitle {
  margin-top: 2rem;
  font-size: 1.4rem;
  text-transform: uppercase;
  color: #00ffff;
  position: relative;
}
.section-subtitle::after {
  content: '';
  position: absolute;
  bottom: -4px; left: 0;
  width: 100%; height: 2px;
  background: #00ffff;
  box-shadow: 0 0 8px #00ffff;
}
.section-list {
  list-style: none;
  padding-left: 0;
  margin-top: 1rem;
}
.section-list li {
  position: relative;
  margin-bottom: 0.8rem;
  padding-left: 1.5rem;
}
.section-list li::before {
  content: '▶';
  position: absolute; left: 0;
  color: #ff00ff;
  text-shadow: 0 0 6px #ff00ff;
  transform: scale(0.8);
}

/* 9. Project Cards */
.card-link {
  display: block;
  text-decoration: none;
  color: inherit;
  margin-bottom: 1rem;
}
.card {
  background: #1a1a2e;
  border-left: 5px solid #00ffff;
  border-radius: 8px;
  padding: 1.2rem;
  box-shadow: 0 2px 6px rgba(0,255,255,0.2);
  transition: transform 0.2s, box-shadow 0.3s, border-color 0.3s;
}
.card:hover {
  transform: translateY(-2px);
  border-color: #ff00ff;
  box-shadow:
    inset 0 0 20px rgba(255,0,255,0.4),
    0 0 16px rgba(255,0,255,0.4);
}
.repo-title {
  position: relative;
  font-size: 1.3rem;
  color: #00ffff;
  text-shadow: 0 0 5px #00ffff80;
  margin-bottom: 0.5rem;
}
.repo-title::after {
  content: '';
  position: absolute; bottom: -4px; left: 0;
  width: 100%; height: 2px;
  background: #00ffff;
  box-shadow: 0 0 8px #00ffff;
}
.repo-desc {
  color: #22ff47;
  margin-bottom: 1rem;
  line-height: 1.4;
}

/* 10. Tag Pills */
.tags {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}
.tag {
  padding: 0.3rem 0.6rem;
  border-radius: 4px;
  font-size: 0.8rem;
  box-shadow: 0 0 4px currentColor;
  transition: box-shadow 0.3s;
}
.tag:nth-child(1) { background: #ff00ff40; color: #ffccff; }
.tag:nth-child(2) { background: #00ffff40; color: #ccffff; }
.tag:nth-child(3) { background: #ffff0040; color: #ffffcc; }
.tag:hover {
  box-shadow: 0 0 8px currentColor, 0 0 12px currentColor;
}

/* 11. README Styling */
.readme {
  color: #e1ff00;
  margin-top: 1rem;
  max-width: 800px;
  line-height: 1.5;
  overflow-x: auto;
}
.readme pre {
  background: #111122;
  border: 1px solid #00ffff;
  padding: 1rem;
  border-radius: 4px;
}
.readme code {
  background: #1a1a2e;
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
}
.readme h1,
.readme h2,
.readme h3 {
  color: #00ffff;
  margin-top: 1rem;
}
.readme a {
  color: #ff00ff;
  text-decoration: underline;
}

/* 12. — Neon AI/ML Ticker — */
.intro-strip {
  
  padding: 0.3rem 0;            
  font-size: 0.9rem;            
  background: #111122;
  color: #00ffff;               
  text-shadow: 0 0 6px #00ffff80;
  border-bottom: 1px solid #00ffff;
  margin-bottom: 0.5rem;         
  animation: flicker 3s infinite;
}

@keyframes flicker {
  0%,19%,21%,23%,25%,54%,56%,100% {
    opacity: 1;
    text-shadow: 0 0 6px #00ffff80;
  }
  20%,24%,55% {
    opacity: 0.6;
    text-shadow: none;
  }
}

.panel {
  margin: 0.5rem 0 2rem 0;   /* top 0.5rem, bottom 2rem */
}
